@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
body {
    background-color: #131210;
}
.App {
  display: relative;
  text-align: center;
}

*{color:white;font-family:'Roboto';font-size:50px}.First-Col{display:-webkit-flex;display:flex;float:left;-webkit-flex-wrap:wrap;flex-wrap:wrap;width:30%}.Second-Col{width:30%;float:right;margin-top:5%}.Input-Board{width:100%}.Input--Edges{width:100px}.List{display:-webkit-flex;display:flex;margin:10px;-webkit-align-items:center;align-items:center}.List div,h1{padding:20px}.Node1,.Node2{box-shadow:0 0 2pt 2pt white;border-radius:50%}.Line{position:absolute;border:1px solid white;color:white}.Btn{background-color:white;width:100px;height:40px;border-radius:10px;font-size:15px;color:#131210}.MainNodes{width:100%;height:100%;position:absolute;left:50%;top:50%;width:600px;height:600px;border:0px solid #ddd;border-radius:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.MainNodes div{width:80px;height:80px;background-color:white;position:absolute;border-radius:50%;left:50%;top:50%;margin:-40px}.MainNodes h1{color:#131210;padding:0px;margin-top:15%}.MainNodes h1:nth-child(1){-webkit-transform:rotate(0) translate(0) rotate(0);transform:rotate(0) translate(0) rotate(0)}.MainNodes h1:nth-child(2){-webkit-transform:rotate(0) translate(0) rotate(0);transform:rotate(0) translate(0) rotate(0)}.MainNodes h1:nth-child(3){-webkit-transform:rotate(0) translate(0) rotate(0);transform:rotate(0) translate(0) rotate(0)}.MainNodes h1:nth-child(4){-webkit-transform:rotate(0) translate(0) rotate(0);transform:rotate(0) translate(0) rotate(0)}.MainNodes h1:nth-child(5){-webkit-transform:rotate(0) translate(0) rotate(0);transform:rotate(0) translate(0) rotate(0)}.MainNodes h1:nth-child(6){-webkit-transform:rotate(0) translate(0) rotate(0);transform:rotate(0) translate(0) rotate(0)}.MainNodes h1:nth-child(7){-webkit-transform:rotate(0) translate(0) rotate(0);transform:rotate(0) translate(0) rotate(0)}.MainNodes div:nth-child(1){-webkit-transform:rotate(0deg) translate(300px) rotate(0deg);transform:rotate(0deg) translate(300px) rotate(0deg)}.MainNodes div:nth-child(2){-webkit-transform:rotate(-51.42857deg) translate(300px) rotate(51.42857deg);transform:rotate(-51.42857deg) translate(300px) rotate(51.42857deg)}.MainNodes div:nth-child(3){-webkit-transform:rotate(-102.85714deg) translate(300px) rotate(102.85714deg);transform:rotate(-102.85714deg) translate(300px) rotate(102.85714deg)}.MainNodes div:nth-child(4){-webkit-transform:rotate(-154.28571deg) translate(300px) rotate(154.28571deg);transform:rotate(-154.28571deg) translate(300px) rotate(154.28571deg)}.MainNodes div:nth-child(5){-webkit-transform:rotate(-205.71429deg) translate(300px) rotate(205.71429deg);transform:rotate(-205.71429deg) translate(300px) rotate(205.71429deg)}.MainNodes div:nth-child(6){-webkit-transform:rotate(-257.14286deg) translate(300px) rotate(257.14286deg);transform:rotate(-257.14286deg) translate(300px) rotate(257.14286deg)}.MainNodes div:nth-child(7){-webkit-transform:rotate(-308.57143deg) translate(300px) rotate(308.57143deg);transform:rotate(-308.57143deg) translate(300px) rotate(308.57143deg)}

.Lines{position:absolute;width:100%;height:100%;transition:all ease 0.2s}.TextLine{border:1px solid white;font-size:30px;font-weight:bold}.LinesSolved{position:relative;width:100%;height:100%;transition:all ease 0.2s}

.Code--Main{text-align:left;border:1px solid white;border-radius:20px;width:90%;padding-right:0px;line-height:20%;overflow:hidden;padding-left:20px}.Code--Main code{font-size:15px}text{color:#666;font-family:monospace;font-size:13px;max-width:100%}.LightBlue{color:#9cdcf0}.DarkBlue{color:#569cd6}.Yellow{color:#dcdcaa}.Purple{color:#da70b3}.Green{color:#b5cea8}.White{color:white}

.Main input{margin-right:20px;margin-top:20px}

.input--main{width:40px;height:40px;font-size:30px;color:#131210;font-family:'Roboto';text-align:center;margin-left:5px}

